<template>
  <div class="container">
    <div class="products">
      <div class="row">
        <div class="col col-sm-9">
          <div class="products__top">
            <div class="products__settings" v-if="view === 'grid'">
              <ul class="filters filters--main">
                <li class="filters__item" v-for="sortKind in sortKinds">
                  <button class="filter filter--main" @click="sortProducts(sortKind)">{{ sortKind }}</button>
                </li>
              </ul>
            </div>
            <div class="products__quantity" v-if="view === 'list'">Number of products: {{ numberOfProducts }}</div>
            <div class="view-toggles">
              <label class="view-toggles__title">Change view</label>
              <ul class="view-toggles__list">
                <li
                  v-for="viewToggle in viewToggles"
                  class="view-toggles__item"
                  :class="{active: isViewActive(viewToggle)}"
                >
                  <button class="view-toggle" @click="changeView(viewToggle)">{{ viewToggle }}</button>
                </li>
              </ul>
            </div>
            <div class="products__quantity" v-if="view === 'list'">Number of products: {{ numberOfProducts }}</div>
          </div>
          <main>
            <products-grid class="products__list" v-if="view === 'grid'">
              <product-grid
                class="products__item"
                v-for="product in products"
                :key="product.id"
                :image="product.image"
                :title="product.title"
              ></product-grid>
            </products-grid>
            <products-list class="products__list" v-if="view === 'list'">
              <product-list
                class="products__item"
                v-for="product in products"
                v-if="product.isFiltered"
                :key="product.id"
                :image="product.image"
                :title="product.title"
                :description="product.description"
                :tags="product.tags"
              ></product-list>
            </products-list>
          </main>
        </div>
        <div class="col col-sm-3">
          <aside>
            <div class="products__filters filters filters--aside">
              <h2 class="filters__title">Sort by:</h2>
              <div class="filter-group" v-for="filterGroup in filters">
                <h3 class="filter-group__title">{{ filterGroup.name }}</h3>
                <ul class="filter-list">
                  <li class="filter-list__item filter" v-for="filter in filterGroup.items">
                    <input
                      type="checkbox"
                      :checked="filter.isActive"
                      @click="toggleFilter(filter)"
                    ><label>{{ filter.name }}</label>
                  </li>
                </ul>
              </div>
            </div>
          </aside>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { includes } from 'lodash';

  import ProductsGrid from './ItemsGrid';
  import ProductGrid from './ItemGrid';
  import ProductsList from './ItemsList';
  import ProductList from './ItemList';
  import ProductsFilters from './Filters';
  import ProductsSettings from './Settings';

  export default {
    name: 'products',

    components: {
      ProductsGrid,
      ProductGrid,
      ProductsList,
      ProductList,
      ProductsFilters,
      ProductsSettings,
    },

    computed: {
      products() {
        return this.$store.state.filteredProducts;
      },

      sortKinds() {
        return this.$store.state.sortKinds;
      },

      viewToggles() {
        return this.$store.state.viewToggles;
      },

      filters() {
        return this.$store.state.filters;
      },

      view() {
        return this.$store.state.view;
      },

      numberOfProducts() {
        return this.$store.state.products.length;
      },

      activeFilters() {
        const filteredFilters = [];

        this.$store.state.filters.forEach((filterGroup) => {
          const groupFilteredFilters = {};
          const filteredItems = [];

          filterGroup.items.forEach((filterItem) => {
            if (filterItem.isActive) {
              filteredItems.push(filterItem);
            }
          });

          if (filteredItems.length) {
            groupFilteredFilters.name = filterGroup.name;
            groupFilteredFilters.items = filteredItems;

            filteredFilters.push(groupFilteredFilters);
          }
        });

        return filteredFilters;
      },
    },

    methods: {
      isViewActive(viewToCheck) {
        return viewToCheck.toLowerCase() === this.view;
      },

      changeView(newView) {
        this.$store.commit('changeView', newView.toLowerCase());
      },

      filterProducts() {
        const products = this.$store.state.products;
        const activeFilters = this.activeFilters;
        const filteredProducts = [];

        products.forEach((product) => {
          const tags = product.tags;
          let isFiltered = false;

          if (!activeFilters.length) {
            isFiltered = true;
          }

          isFiltered = activeFilters.every((activeFilterGroup) => {
            for (let i = 0; i < tags.length; i += 1) {
              if (activeFilterGroup.name === tags[i].name) {
                const tagsGroupItems = tags[i].items;
                const activeFilterGroupItems = activeFilterGroup.items;

                return activeFilterGroupItems.every((activeFilterGroupItem) => {
                  return includes(tagsGroupItems, activeFilterGroupItem.name);
                });
              }
            }

            return false;
          });

          if (isFiltered) {
            filteredProducts.push(product);
          }
        });

        this.$store.commit('filterProduct', filteredProducts);
      },

      toggleFilter(filter) {
        filter.isActive = !filter.isActive;

        this.filterProducts();
      },

      sortProducts(sortKind) {
        switch (sortKind) {
          case 'Newest':
            this.sortByNewest();
            break;
          case 'A-Z':
            this.sortByAlphabet();
            break;
          case 'Popular':
            this.sortByPopularity();
            break;
          default:
            console.log('sortProducts: unexpected case');
        }

        this.filterProducts();
      },

      sortByNewest() {
        const products = this.$store.state.products;

        products.sort((productA, productB) => {
          const productADate = new Date(productA.releaseDate).getTime();
          const productBDate = new Date(productB.releaseDate).getTime();

          if (productADate > productBDate) return -1;
          if (productADate < productBDate) return 1;

          return 0;
        });

        this.$store.commit('updateProducts', products);
      },

      sortByAlphabet() {
        const products = this.$store.state.products;

        products.sort((productA, productB) => {
          if (productA.title < productB.title) return -1;
          if (productA.title > productB.title) return 1;

          return 0;
        });

        this.$store.commit('updateProducts', products);
      },

      sortByPopularity() {
        const products = this.$store.state.products;

        products.sort((productA, productB) => {
          if (productA.popularity > productB.popularity) return -1;
          if (productA.popularity < productB.popularity) return 1;

          return 0;
        });

        this.$store.commit('updateProducts', products);
      },
    },

    created() {
      this.filterProducts();
    },
  };
</script>

<style>
  *,
  *::before,
  *::after {
    box-sizing: border-box;
  }

  body {
    margin: 0;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin-top: 0;
  }

  .container {
    max-width: 1225px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
  }

  .row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
  }

  .col {
    padding-left: 15px;
    padding-right: 15px;
  }

  .col-sm-4 {
    width: 33.3333%;
  }

  .col-sm-9 {
    width: 75%;
  }

  .col-sm-3 {
    width: 25%;
  }
</style>



